/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
button, input[type="submit"] {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  appearance: none;
}
input[type="text"] {
  padding: 0;
  border: none;
  border-radius: 0;
  outline: none;
  background: none;
}
* {
  box-sizing: border-box;
  letter-spacing: 0.025em;
}

$COLOR_GRAY: #E9EBF8;
$COLOR_NAVY: #0B1D51;
$COLOR_YELLOW: #F0E100;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $COLOR_NAVY;
  background-color: $COLOR_GRAY;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

// ---------- Apollo ----------
.apollo {
  margin: 0 auto;
  padding: 48px 0;
  width: 720px;
}

// ---------- Auth ----------
.auth {
  &-button {
    margin: 0 auto;
    padding: 16px 0;
    display: block;
    width: 160px;
    color: $COLOR_YELLOW;
    background-color: $COLOR_NAVY;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 700;
  }
}

// ---------- Auth ----------
.todo {
  margin: 0 auto;
  padding: 48px 0 32px;
  width: 720px;
  &-form {
    width: 100%;
    display: flex;
    align-items: bottom;
    position: relative;
    input[type="text"] {
      margin: 0 16px 0 0;
      padding: 8px;
      flex: 1;
      border-bottom: 2px solid $COLOR_NAVY;
      font-size: 16px;
      font-weight: 700;
      &::placeholder {
        color: rgba($COLOR_NAVY, 0.5);
      }
    }
    input[type="submit"] {
      width: 48px;
      height: 48px;
      color: $COLOR_YELLOW;
      background-color: $COLOR_NAVY;
      font-weight: 700;
      border-radius: 4px;
    }
    span {
      display: block;
      position: absolute;
      top: 50%;
      right: 8px;
      transform: translateY(-50%);
      svg {
        width: 32px;
        height: 32px;
        color: $COLOR_YELLOW;
      }
    }
  }
  &-list {
    margin: 32px 0 24px 0;
  }
  &-item {
    padding: 24px 0;
    display: flex;
    align-items: center;
    width: 100%;
    border-bottom: 1px solid rgba($COLOR_NAVY, 0.125);
    &:last-of-type {
      border-bottom: none;
    }
    &__head {
      width: 32px;
      height: 32px;
      &__check {
        width: 32px;
        height: 32px;
        background-color: $COLOR_GRAY;
        border-radius: 24px;
        border: 2px solid $COLOR_NAVY;
        cursor: pointer;
        svg {
          display: none;
          width: 42px;
          height: 42px;
          position: relative;
          top: -8px;
          left: -4px;
          color: $COLOR_YELLOW;
        }
        &.is-done {
          background-color: $COLOR_NAVY;
        }
        &.is-done svg {
          display: block;
        }
      }
    }
    &__body {
      margin: 0 16px;
      flex: 1;
      &__title {
        width: 100%;
        font-weight: 700;
      }
      &__content {
        width: 100%;
        margin: 8px 0 0 0;
        font-size: 12px;
        &.is-empty {
          color: rgba($COLOR_NAVY, 0.5);
        }
      }
    }
    &__foot {
      width: 20px;
      height: 20px;
      button {
        width: 20px;
        height: 20px;
        svg {
          width: 20px;
          height: 20px;
          color: $COLOR_NAVY;
        }
      }
    }
  }
  &-modal {

  }
}